
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: none;
    outline: none;
    scrollbar-width: thin;
    scroll-behavior: smooth;
    scrollbar-color: rgba(99, 102, 241, 0.3) rgba(12, 5, 53, 0.356);
    font-family: 'Poppins', sans-serif;
}


:root{
  --bg-color:#1f242d;
  --second-bg-color:#323946;
  --text-color:#fff;
  --main-color:#0effb0;
}


html{
  font-size:62.5% ;
  overflow-x: hidden;

}

body{
  background: var(--bg-color);
  color:var(--text-color);
}

html,
body {
	overflow-x: hidden;
	background: #030014;
	scroll-behavior: smooth !important;
}

@keyframes blob {
	0%,
	100% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(-20px);
	}
}

.animate-blob {
	animation: blob 8s infinite;
}

.animation-delay-2000 {
	animation-delay: 2s;
}

.animation-delay-4000 {
	animation-delay: 4s;
}

/* Enhanced Custom Scrollbar Styles */
::-webkit-scrollbar {
	width: 8px;
	height: 8px;
	background-color: transparent;
  }
  
  ::-webkit-scrollbar-track {
	background: rgba(3, 0, 20, 0.4);
	backdrop-filter: blur(8px);
	-webkit-backdrop-filter: blur(8px);
	border: 1px solid rgba(99, 102, 241, 0.1);
  }
  
  ::-webkit-scrollbar-thumb {
	background: rgba(99, 102, 241, 0.3);
	backdrop-filter: blur(4px);
	-webkit-backdrop-filter: blur(4px);
	border-radius: 20px;
	border: 1px solid rgba(255, 255, 255, 0.1);
	box-shadow: 
	  inset 0 0 20px rgba(168, 85, 247, 0.2),
	  0 0 10px rgba(99, 102, 241, 0.2);
	transition: all 0.3s ease;
  }
  
  ::-webkit-scrollbar-thumb:hover {
	background: rgba(99, 102, 241, 0.5);
	border: 1px solid rgba(168, 85, 247, 0.3);
	box-shadow: 
	  inset 0 0 30px rgba(168, 85, 247, 0.3),
	  0 0 15px rgba(99, 102, 241, 0.3);
  }
  
  /* When scrollbar is in active/clicked state */
  ::-webkit-scrollbar-thumb:active {
	background: rgba(99, 102, 241, 0.6);
	border: 1px solid rgba(168, 85, 247, 0.4);
	box-shadow: 
	  inset 0 0 40px rgba(168, 85, 247, 0.4),
	  0 0 20px rgba(99, 102, 241, 0.4);
  }
  
  ::-webkit-scrollbar-corner {
	background: transparent;
  }

  [data-aos] {
	will-change: transform, opacity !important;
  }



 @keyframes shine {
  from {
	     left: -100%;
  }
	  to {
	    left: 200%;
	   }
	 }
	 .animate-shine {
   animation: shine 1.5s ease-in-out infinite;
 }

  
/* App.css */
html {
	scroll-behavior: smooth;
  }
